import React from 'react';
import { useProfileInfo } from 'hooks/useProfileInfo';
import { Post } from 'models/fragments';
import { getSrc } from 'gatsby-plugin-image';

const getDefaultData = (authorName: string) => {
  return {
    '@context': 'https://schema.org',
    '@type': 'NewsArticle',
    mainEntityOfPage: {
      '@type': 'WebPage',
      '@id': 'https://evan-moon.github.io',
    },
    author: {
      '@type': 'Person',
      name: authorName,
    },
    publisher: {
      '@type': 'Person',
      name: authorName,
    },
  };
};

const StructuredPostData = ({ post }: { post: NonNullable<Post> }) => {
  const profile = useProfileInfo();

  if (profile == null) {
    return null;
  }

  const defaultData = getDefaultData(profile.site?.siteMetadata?.author?.name ?? 'Evan Moon');

  const thumbnail = post.frontmatter?.thumbnail?.childImageSharp;
  const thumbnailSrc = thumbnail == null ? '' : getSrc(thumbnail);

  const data = {
    ...defaultData,
    headline: post.frontmatter?.title,
    image: [thumbnailSrc],
    datePublished: post.frontmatter?.date,
    dateModified: post.frontmatter?.date,
  };

  return <script type="application/ld+json" dangerouslySetInnerHTML={{ __html: JSON.stringify(data) }} />;
};

export default StructuredPostData;
